* {
  font-family: "Lato", sans-serif;
  /* margin-left: 0.5rem;
  margin-right: 0.5rem; */

  color: #707070;
}
.invoicedesigncontainer {
  margin: 1rem;
}
#formcontainer {
  margin-top: 1rem;
}
.info {
  display: flex;
  justify-content: center;
}
.userdetail {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
}

.del {
  margin-right: 5px;
  cursor: pointer;
}
.companynamecontainer {
  display: flex;
  justify-content: center;
  margin: 2rem;
}
.userinvoices {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.inputcomponent {
  margin-left: 14.2px;
  border: none;
}
.listbtn,
.btns {
  display: flex;
  justify-content: center;
  margin-top: 2rem;
  align-items: center;
}

.companyname {
  font-family: "Lato", sans-serif;
  color: #212121;
  font-size: 3rem;
  font-weight: 800;
}
.userinfo {
  display: flex;
  height: 100%;
  border-top: 2px solid grey;
  border-bottom: 2px solid grey;
  text-align: center;
  justify-content: space-between;
  margin-bottom: 1rem;
}
.purpose {
  border-right: 2px solid grey;
  padding: 0.2rem;
  justify-content: space-between;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  color: rgb(0, 102, 255);
}

