.invoiceheading,
.invoicecol {
  left: 0;
  padding: 0%;
  margin: 0%;
}

table {
  color: #212121;

  border-collapse: collapse;
  width: 100%;
}

td,
th {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
}

tr:first-child() {
  background-color: #b4b4b4;
}

.result {
  display: flex;
  justify-content: flex-end;
  margin-top: 1.4rem;
}
.container {
  display: flex;
  justify-content: space-between;
  margin-top: 1.4rem;
}
.amount,
.taxes {
  display: block;
  margin: 0.2rem;
}

.tccontainer {
  width: 50%;
}

.tccontainer > h2 {
  border-bottom: 1px solid #b4b4b4;
}

.btn {
  height: 50px;
  width: 100px;
  cursor: pointer;
  border-radius: 4px;
  background-color: rgb(65, 141, 255);
  color: #fff;
}

.pricecontainer > h1 {
  float: right;
  font-weight: bolder;
  font-size: 5rem;
}

.pricecontainer > h2 {
  margin-left: 50%;
  float: right;
}
.greet {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;

  color: rgb(0, 102, 255);
}

.ownerinfo {
  display: flex;
  height: 100%;
  border-top: 2px solid grey;
  border-bottom: 2px solid grey;
  text-align: center;
  justify-content: space-between;
  margin-bottom: 1rem;
}
.ownername,
.owneraddress,
.ownerotherinfo {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;

  color: rgb(0, 102, 255);
}

.owneraddress,
.ownerotherinfo {
  width: 25%;
}

.textField {
  margin: 10px;
  /* background-color: aqua; */
}

.formcontainer {
  display: flex;
}
.clientinfo {
  display: flex;
  /* justify-content: center; */
}
.clientinfo > input,
.productinfo > input,
.duedate {
  padding: 0.5rem;
  border-radius: 4px;
  width: 12.5rem;
  height: 2rem;
  margin: 0.75rem;
}
.create {
  display: flex;
  justify-content: flex-end;
}

